.bubble {
  background: linear-gradient(
    45deg,
    $left-gradient-color,
    $right-gradient-color
  );
  opacity: 0.4;
  margin-left: $f55;
  border-radius: 50%;
  height: $f400 + $f178;
  z-index: -10;
  
  &-image {
    content: " ";
    position: relative;
    height: $f400 + $f178;
    border-radius: 50%;
  }
  &.animate {
    animation-duration: 5s;
    animation-iteration-count: infinite;
  }
  &.small {
    width: $f55;
    height: $f55;
    &.animate {
      animation-name: bubble-animation;
      animation-delay: random($limit: 5) + s;
    }
    
  }
  &.semi-middle{
    width: $f144;
    height: $f144;
    &.animate{
      animation-name: bubble-animation;
      animation-delay: random($limit: 5) + s;
    }
  }
  &.middle {
    width: $f233;
    height: $f233;
    &.animate {
      animation-name: bubble-animation;
      animation-delay: random($limit: 5) + s;
    }
    
  }
  &.big {
    width: $f377;
    height: $f377;
    &.animate {
      animation-name: bubble-animation;
      animation-delay: random($limit: 5) + s;
    }
    
  }
}
@keyframes bubble-animation {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-$f10, $f21);
    
  }
  100% {
    transform: translate(0, 0);
  }
}